// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import { Button, MessageBox, Actionsheet, Toast, DatetimePicker, Loadmore } from 'mint-ui';


Vue.config.productionTip = false;

Vue.component(Button.name, Button);
Vue.component(Actionsheet.name, Actionsheet);
Vue.component(DatetimePicker.name, DatetimePicker);
Vue.component(Loadmore.name, Loadmore);

Object.defineProperty(Vue.prototype, "$messagebox", { value: MessageBox })
Object.defineProperty(Vue.prototype, '$toast', { value: Toast, writable: false });

/* eslint-disable no-new */
document.addEventListener('deviceready', function () {
  new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
  })

  window.navigator.splashscreen.hide()
}, false)




