<template>
  <div class="models">
    <div id="viewer" class="viewer"></div>
  </div>
</template>

<script>
export default {
  name: "Models",
  data() {
    return {
      oViewer: null
    };
  },
  methods: {
    initializerModel() {}
  },
  mounted() {
    let oViewer = null;
    let viewer = document.getElementById("viewer");
    // http://static.cloud.bimbdip.com/filebim/wendang/2/model/201711/DEMO/3d.svf
    let docs = [
      {
        path: "1/Assem2F.svf",
        name: "Scene"
      }
    ];
    let options = { docid: docs[0].path, env: "Local" };

    oViewer = new Autodesk.Viewing.Private.GuiViewer3D(viewer, {}); // With toolbar

    Autodesk.Viewing.Initializer(options, function() {
      oViewer.initialize();
      oViewer.addEventListener(Autodesk.Viewing.GEOMETRY_LOADED_EVENT, function(
        event
      ) {
        //oViewer.removeEventListener (Autodesk.Viewing.GEOMETRY_LOADED_EVENT, arguments.callee) ;
        oViewer.fitToView(true);
        setTimeout(function() {
          oViewer.autocam.setHomeViewFrom(oViewer.navigation.getCamera());
        }, 1000);
      });
      oViewer.loadModel(options.docid);
    });
  }
};
</script>

<style scoped>
.viewer {
  width: 100%;
  height: 100%;
}
</style>




