<template>
  <div class="home">
    <h1>{{ msg }}</h1>
    <div>
      <mt-button type="default" @click.native="handleClick">default</mt-button>
      <mt-button type="primary" @click.native="sheetVisible = !sheetVisible">primary</mt-button>
      <mt-button type="danger" @click.native="handlePicker">danger</mt-button>
      <mt-button icon="more" @click.native="gotoList">更多</mt-button>
      <mt-button type="primary" @click.native="handleCamera">打开相机</mt-button>
      <mt-button type="primary" @click.native="gotoModel">查看模型</mt-button>

      <div>
        <img :src="imageData" alt="img" width="100" height="100">
      </div>

      <mt-actionsheet :actions="actions" v-model="sheetVisible">
      </mt-actionsheet>

      <mt-datetime-picker ref="picker" type="date" v-model="pickerValue" @confirm="handleConfirm">
      </mt-datetime-picker>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      sheetVisible: false,
      actions: [
        { name: "今日", id: "0", method: this.handlefn },
        { name: "昨天", id: "1", method: this.handlefn },
        { name: "本周", id: "2", method: this.handlefn },
        { name: "本月", id: "3", method: this.handlefn },
        { name: "本年", id: "4", method: this.handlefn },
        { name: "全部", id: "5", method: this.handlefn }
      ],
      pickerValue: "",
      imageData:
        "https://imgsa.baidu.com/exp/w=500/sign=00edd4094d34970a4773102fa5cad1c0/38dbb6fd5266d016a11a820e9b2bd40735fa3590.jpg"
    };
  },
  methods: {
    handleClick() {
      console.log("enter");
      this.$messagebox({
        title: "提示",
        message: "确定执行此操作?",
        showCancelButton: true
      });
    },
    handlefn() {
      this.$toast({
        message: "操作成功",
        iconClass: "icon icon-success"
      });
    },
    handlePicker() {
      this.$refs.picker.open();
    },
    handleConfirm(val) {
      console.log(val);
    },
    gotoList() {
      this.$router.push({
        name: "List"
      });
    },
    onSuccess(imageData) {
      this.imageData = "data:image/jpeg;base64," + imageData;
    },
    onFail(message) {
      this.$messagebox({
        title: "提示",
        message: "Failed because: " + message
      });
    },
    handleCamera() {
      const self = this;
      navigator.camera.getPicture(self.onSuccess, self.onFail, {
        quality: 50,
        destinationType: Camera.DestinationType.DATA_URL
      });
    },
    gotoModel() {
      this.$router.push({
        name: "Model"
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



